.swiper {
  height: 540px;
  @include r(767) {
    height: 220px;
    padding: 0 10px;
    padding-top: 30px; } }
// .swiper-intro
//   display: none
.swiper-intro, .swiper-intro-max {
  height: 100%;
  @include r(767) {
    overflow: visible; }
  .container {
    position: relative; } }
.swiper-slide {
  .max-slide {
    display: none; }
  .min-slide, .max-slide {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    position: relative;
    width: 100%;
    height: 100%; }
  .slide__img {
    overflow: hidden;
    height: 190px;
    width: 100%;
    border-radius: 8px;
    box-shadow: 0 20px 43px 0 rgba(206, 209, 217, 0.62);
    img {
      width: 100%;
      height: 100%;
      @include object-fit(cover); } }
  p {
    font-size: 14px !important;
    font-weight: 500 !important;
    color: $white !important;
    border-radius: 30px;
    background-color: $pale-rose;
    box-shadow: 0 0 29px 0 rgba(133, 135, 142, 0.29);
    padding: 10px 15px;
    display: inline-block;
    position: absolute;
    top: -20px;
    right: 10px;
    margin-top: 0 !important; } }
.swiper-button-next {
  right: 200px; }
.swiper-button-prev {
  right: 620px !important; }
.swiper-button-next, .swiper-button-prev {
  bottom: 85px;
  top: auto;
  background: none;
  opacity: 1 !important;
  width: 56px;
  height: 56px;
  box-shadow: 0 20px 43px 0 rgba(230, 233, 240, 0.62);
  background: $white;
  border-radius: 100%;
  @include center;
  @include r(767) {
    display: none; }
  i {
    font-size: 50px;
    line-height: 1;
    color: $pale-rose;
    position: relative;
    left: 45%;
    top: 45.5%;
    transform: translate(-50%, -50%);
    @include tr;
    @include center; }
  &:hover {
    i {
      color: $cool-grey; } } }
.swiper-button-next {
  &:before {
      content: 'Следующий букет';
      text-align: right;
      font-size: 12px;
      font-weight: 500;
      color: $white;
      padding-right: 20px;
      line-height: 2;
      width: 710px;
      height: 1px;
      background: $white;
      position: absolute;
      right: 100%;
      top: 53%;
      transform: translate(0, -50%);
      @include r(900) {
        width: 600px; } }
  i {
    transform: rotate(180deg) translate(50%, 50%); } }
.swiper-button-prev {
  right: 390px;
  left: auto;
  bottom: 84px; }



@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .swiper-button-prev {
    i {
      top: 47%; } }
  .swiper-button-next, {
    i {
      top: 43%; } } }
